.App {
  text-align: center;
  padding-bottom: 20px;
}
 
.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

*{
  padding: 0px;
  margin: 0px;
}


.flexbody{
display: flex;
flex-direction: row;
min-height: fit-content;
height: 100vh;
/* position: fixed; */
}
.slide1{
height: 100vh;
background: linear-gradient(to bottom left, rgb(8, 8, 36), rgb(150, 21, 21));
width: 24vw;
position: fixed;
margin-right: 100px;
}

.slide1 h3 {


  margin-top:20px ;
  /* padding: 10px; */
  color: white;
 border: 1px solid white;
 border-radius: 150px;
 transition: 1s;
 width: 24vw;
 text-decoration: none;  
 font-size: 14px;
 margin-bottom:1vh;


}

.slide1  h3 .link{
  color: white;
  text-decoration: none;
}

.slide1 h3 .link:hover{
  color: black;
}

.slide1 h3:hover{
background-color: white;
color: black;
}

.slide1 h3 Link {
  color: aqua;
}

